body {
    overflow: hidden;
    color: #fff;
    background: black;
    height: 100vh;
  }
  
  @-webkit-keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  @keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  
  /* 3D Cube styles */
  .cube:hover {
    cursor: pointer;
  }
  
  @-webkit-keyframes turn {
    from {
      -webkit-transform: rotate3d(0, 0, 0, 0);
              transform: rotate3d(0, 0, 0, 0);
    }
    to {
      -webkit-transform: rotate3d(1, 1, 0, 360deg);
              transform: rotate3d(1, 1, 0, 360deg);
    }
  }
  
  @keyframes turn {
    from {
      -webkit-transform: rotate3d(0, 0, 0, 0);
              transform: rotate3d(0, 0, 0, 0);
    }
    to {
      -webkit-transform: rotate3d(1, 1, 0, 360deg);
              transform: rotate3d(1, 1, 0, 360deg);
    }
  }
  
  .cube {
    margin-top: 250px;
    margin-left: 43%;
    width: 17rem;
    height: 17rem;
    cursor: pointer;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: turn 10s linear infinite;
            animation: turn 10s linear infinite;
    
  }
  
  .face {
    border-radius: 1px;
    box-shadow: 5px 10px 20px rgba(180, 28, 66, 0.3);
    width: 10rem;
    height: 10rem;
    background-image: radial-gradient(
      circle at top,
      rgb(22, 140, 219),
      rgb(66, 23, 167),
      rgb(211, 110, 231)
    );
    position: absolute;
    opacity: 0.5;
    font-family: Arial, sans-serif;
    font-size: 2rem;
    -webkit-animation: showup 6s linear 1s 1 forwards;
            animation: showup 6s linear 1s 1 forwards; 
    opacity: 0;
    text-align: center
  }
  
  @-webkit-keyframes showup {
    from{ 
      opacity: 0;
    }
    to{
      opacity: 0.6;
    }
    
  }
  
  @keyframes showup {
    from{ 
      opacity: 0;
    }
    to{
      opacity: 0.6;
    }
    
  }

  .front {
    -webkit-transform: translateZ(80px);
            transform: translateZ(80px);
  }
  
  .back {
    -webkit-transform: translateZ(-80px) rotateY(180deg);
            transform: translateZ(-80px) rotateY(180deg);
  }
  
  .left {
    -webkit-transform: translateX(-80px) rotateY(-90deg);
            transform: translateX(-80px) rotateY(-90deg);
  }
  
  .right {
    -webkit-transform: translateX(80px) rotateY(90deg);
            transform: translateX(80px) rotateY(90deg);
  }
  
  .top {
    -webkit-transform: translateY(-80px) rotateX(90deg);
            transform: translateY(-80px) rotateX(90deg);
  }
  
  .bottom {
    -webkit-transform: translateY(80px) rotateX(-90deg);
            transform: translateY(80px) rotateX(-90deg);
  }
  
  @-webkit-keyframes spinX {
    from {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
    }
    to {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
  }
  
  @keyframes spinX {
    from {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
    }
    to {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
  }
  
  @-webkit-keyframes spinY {
    from {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
    }
    to {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
  }
  
  @keyframes spinY {
    from {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
    }
    to {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
  }
  
  @-webkit-keyframes spinZ {
    from {
      -webkit-transform: rotateZ(0);
              transform: rotateZ(0);
    }
    to {
      -webkit-transform: rotateZ(360deg);
              transform: rotateZ(360deg);
    }
  }
  
  @keyframes spinZ {
    from {
      -webkit-transform: rotateZ(0);
              transform: rotateZ(0);
    }
    to {
      -webkit-transform: rotateZ(360deg);
              transform: rotateZ(360deg);
    }
  }
  
  @-webkit-keyframes color1 {
    0% {
      border-bottom-color: rgb(101, 41, 241);
    }
    50% {
      border-bottom-color: rgb(45, 7, 134);
    }
    100% {
      border-bottom-color: rgb(101, 41, 241);
    }
  }
  
  @keyframes color1 {
    0% {
      border-bottom-color: rgb(101, 41, 241);
    }
    50% {
      border-bottom-color: rgb(45, 7, 134);
    }
    100% {
      border-bottom-color: rgb(101, 41, 241);
    }
  }

.script{
  font-family: Orbitron, sans-serif;
  color:rgba(255,255,255,.3); 
  background: -webkit-linear-gradient(transparent, transparent),               url(https://i.imgur.com/WyMyxQ6.png) repeat;
  /* clipping webkit magic */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  /* Animation */
  -webkit-animation: glow 3s infinite alternate;
          animation: glow 3s infinite alternate;
  -webkit-animation-timing-function: cubic-bezier(1,.3,.79,.81);
          animation-timing-function: cubic-bezier(1,.3,.79,.81);
}

@-webkit-keyframes glow {
  0% {
    text-shadow: 0 0 1px rgba(236, 236, 236, 0.8),
                 0 0 3px rgba(255,255,255,.8),
                 0 0 5px rgba(97, 219, 90, 0.8);
  }
  50% {
    text-shadow: 0 0 2px rgba(255,255,255,.8),
                 0 0 5px rgba(255,255,255,.8);
  }
  52% {
    text-shadow: 0 0 5px rgba(97, 219, 90, 0.5);
  }
  54% {
    text-shadow: 0 0 2px rgba(255,255,255,.8),
                 0 0 5px rgba(255,255,255,.8);
  }
  100% {
    text-shadow: 0 0 2px rgba(255,255,255,.8),
                 0 0 5px rgba(255,255,255,.8),
                 0 0 5px rgba(97, 219, 90, 0.8);
  }
}

@keyframes glow {
  0% {
    text-shadow: 0 0 1px rgba(255,255,255,.8),
                 0 0 3px rgba(255,255,255,.8),
                 0 0 5px rgba(97, 219, 90, 0.8);
  }
  50% {
    text-shadow: 0 0 2px rgba(255,255,255,.8),
                 0 0 5px rgba(255,255,255,.8);
  }
  52% {
    text-shadow: 0 0 5px rgba(97, 219, 90, 0.5);
  }
  54% {
    text-shadow: 0 0 2px rgba(255,255,255,.8),
                 0 0 5px rgba(255,255,255,.8);
  }
  100% {
    text-shadow: 0 0 2px rgba(255,255,255,.8),
                 0 0 5px rgba(255,255,255,.8),
                 0 0 5px rgba(97, 219, 90, 0.8);
  }
}

